import React, { useState } from 'react'

import DialogTitle from '@material-ui/core/DialogTitle'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import { useDispatch } from 'react-redux'

import { editMessage, removeMessage } from './chatSlice'

export default function EditMessageDialog({ open, onClose, message, chat }) {
    const [messageText, setMessageText] = useState(message.text)
    const dispatch = useDispatch()

    const handleMessageChange = (event) => setMessageText(event.target.value)

    const handleDelete = () => {
        onClose()

        dispatch(removeMessage({ chat, id: message.id }))
    }

    const handleSubmit = () => {
        onClose()

        dispatch(
            editMessage({
                chat,
                message: {
                    ...message,
                    text: messageText,
                },
            })
        )
    }

    return (
        <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
            <DialogTitle id="form-dialog-title">
                –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —Å–æ–æ–±—â–µ–Ω–∏—è
            </DialogTitle>
            <DialogContent>
                <TextField
                    value={messageText}
                    onChange={handleMessageChange}
                    autoFocus
                    margin="dense"
                    label="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —Å–æ–æ–±—â–µ–Ω–∏—è"
                    type="text"
                    fullWidth
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleDelete} color="primary">
                    –£–¥–∞–ª–∏—Ç—å
                </Button>
                <Button onClick={onClose}>–û—Ç–º–µ–Ω–∞</Button>
                <Button onClick={handleSubmit} color="primary">
                    –ò–∑–º–µ–Ω–∏—Ç—å
                </Button>
            </DialogActions>
        </Dialog>
    )
}
